import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Result from '@/pages/Result'
Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/result',
      component: Result
    }
  ]
})



