<template>
<v-ons-page>
  <v-ons-toolbar class="home-toolbar">
    <div class="center">{{ msg }}</div>
  </v-ons-toolbar>

  <div class="header">
    <img src="../assets/logo.png" alt="vue-logo">
  </div>

<div style="text-align: center;">
  <button><p style="margin:0;font-size:20px">{{ flag }}</p></button>
</div>
<div style="text-align: center;">
  <div v-show="true" ref="vue" class="ani"></div>
</div>


</v-ons-page>
</template>

<script>
import * as THREE from 'three'
import router from '../router'

export default {
  name: 'Home',
  data () {
    const width = 500
    const height = 500

    const scene = new THREE.Scene()

    const renderer = new THREE.WebGLRenderer()
    renderer.setPixelRatio(window.devicePixelRatio) // retina対応
    renderer.setSize(width, height)

    const camera = new THREE.PerspectiveCamera(90, width / height, 1, 100)
    camera.position.set(0, 0, 20)

    const light = new THREE.DirectionalLight(0xffffffff)
    light.position.set(0, 0, 20)

    const geometry = new THREE.BoxGeometry(10, 10, 10)
    const material = new THREE.MeshStandardMaterial({ color: 0xeeeeee })
    const cube = new THREE.Mesh(geometry, material)

    const obj = new THREE.Group()

    const sphere1 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0xff0000})
    )
    sphere1.rotation.y = Math.PI
    sphere1.position.set(0, 0, -5.001)

    const sphere21 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere21.rotation.y = Math.PI * 3 / 2
    sphere21.position.set(-5, 2.5, 2.5)

    const sphere22 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere22.rotation.y = Math.PI * 3 / 2
    sphere22.position.set(-5.001, -2.5, -2.5)

    const sphere31 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere31.rotation.x = Math.PI / 2
    sphere31.position.set(0, -5.001, 0)

    const sphere32 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere32.rotation.x = Math.PI / 2
    sphere32.position.set(2.5, -5.001, -2.5)

    const sphere33 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere33.rotation.x = Math.PI / 2
    sphere33.position.set(-2.5, -5.001, 2.5)

    const sphere41 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere41.rotation.x = Math.PI * 3 / 2
    sphere41.position.set(2.5, 5.001, 2.5)

    const sphere42 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere42.rotation.x = Math.PI * 3 / 2
    sphere42.position.set(2.5, 5.001, -2.5)

    const sphere43 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere43.rotation.x = Math.PI * 3 / 2
    sphere43.position.set(-2.5, 5.001, 2.5)

    const sphere44 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere44.rotation.x = Math.PI * 3 / 2
    sphere44.position.set(-2.5, 5.001, -2.5)

    const sphere51 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere51.rotation.y = Math.PI / 2
    sphere51.position.set(5.001, 0, 0)

    const sphere52 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere52.rotation.y = Math.PI / 2
    sphere52.position.set(5.001, 2.5, 2.5)

    const sphere53 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere53.rotation.y = Math.PI / 2
    sphere53.position.set(5.001, -2.5, 2.5)

    const sphere54 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere54.rotation.y = Math.PI / 2
    sphere54.position.set(5.001, -2.5, -2.5)

    const sphere55 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere55.rotation.y = Math.PI / 2
    sphere55.position.set(5.001, 2.5, -2.5)

    const sphere61 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere61.position.set(2.5, 2.5, 5.001)

    const sphere62 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere62.position.set(2.5, 0, 5.001)

    const sphere63 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere63.position.set(2.5, -2.5, 5.001)

    const sphere64 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere64.position.set(-2.5, -2.5, 5.001)

    const sphere65 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere65.position.set(-2.5, 0, 5.001)

    const sphere66 = new THREE.Mesh(
      new THREE.CircleGeometry(1, 30, 30),
      new THREE.MeshLambertMaterial({color: 0x000000})
    )
    sphere66.position.set(-2.5, 2.5, 5.001)

    return {
      msg: 'Shake the dice',
      flag: 'ON',
      isDraw: false,
      point: '',
      scene: scene,
      renderer: renderer,
      camera: camera,
      light: light,
      obj: obj,
      cube: cube,
      sphere1: sphere1,
      sphere21: sphere21,
      sphere22: sphere22,
      sphere31: sphere31,
      sphere32: sphere32,
      sphere33: sphere33,
      sphere41: sphere41,
      sphere42: sphere42,
      sphere43: sphere43,
      sphere44: sphere44,
      sphere51: sphere51,
      sphere52: sphere52,
      sphere53: sphere53,
      sphere54: sphere54,
      sphere55: sphere55,
      sphere61: sphere61,
      sphere62: sphere62,
      sphere63: sphere63,
      sphere64: sphere64,
      sphere65: sphere65,
      sphere66: sphere66
    }
  },
  created () {
    this.scene.add(this.camera)
    this.scene.add(this.light)
    this.obj.add(this.cube)
    this.obj.add(this.sphere1)
    this.obj.add(this.sphere21)
    this.obj.add(this.sphere22)
    this.obj.add(this.sphere31)
    this.obj.add(this.sphere32)
    this.obj.add(this.sphere33)
    this.obj.add(this.sphere41)
    this.obj.add(this.sphere42)
    this.obj.add(this.sphere43)
    this.obj.add(this.sphere44)
    this.obj.add(this.sphere51)
    this.obj.add(this.sphere52)
    this.obj.add(this.sphere53)
    this.obj.add(this.sphere54)
    this.obj.add(this.sphere55)
    this.obj.add(this.sphere61)
    this.obj.add(this.sphere62)
    this.obj.add(this.sphere63)
    this.obj.add(this.sphere64)
    this.obj.add(this.sphere65)
    this.obj.add(this.sphere66)
    this.scene.add(this.obj)
    this.obj.rotation.x += Math.PI * 1 / 8
    this.renderer.render(this.scene, this.camera)
  },
  mounted () {
    this.$refs.vue.appendChild(this.renderer.domElement)
  },
  methods: {
    animate (flag) {
      if (flag) {
        this.point = requestAnimationFrame(this.animate)
        this.obj.rotation.x += 0.05
        this.obj.rotation.z += 0.05
        this.renderer.render(this.scene, this.camera)
      } else {
        cancelAnimationFrame(this.point)
      }
    },
    doOn () {
      this.isDraw = !this.isDraw
      if (this.isDraw) {
        this.flag = 'OFF'
        this.animate(this.isDraw)
        setTimeout(() => {
          router.push('/')
        }, 3000)
      } else {
        this.flag = 'ON'
        this.animate(this.isDraw)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center
}

img {
  max-width: 300px
}

ons-list-title {
  text-transform: none
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px
}

ons-card {
  text-align: center
}

ons-list-item, ons-card {
  cursor: pointer
}

.ani {
  /* animation: fadeOut 3s ease-out 0s 1 forwards */
}

@keyframes fadeIn {
  0%{
    opacity: 0
  }
  100%{
    opacity: 1
  }
}

@keyframes fadeOut {
  0%{
    opacity: 1
  }
  80%{
    opacity: 1
  }
  100%{
    opacity: 0
  }
}
</style>



